.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ant-layout-header {
  background-color: rgb(36, 39, 39);
  color: white;
  display: flex;
  justify-content: flex-end;
  font-size: 20px;
}

.ant-layout-header a {
  color: white;
}
.ant-layout-header a:hover {
  color: white;
}
.ant-layout-content {
  text-align: center;
}

.ant-layout-footer {
  background-color: rgb(36, 39, 39);
  text-align: center;
}

.nameHeader {
  left: -9%;
  color: white;
  position: relative;
}
@media only screen and (min-width: 600px) {
  .nameHeader {
    color: "white";
    position: relative;
    left: -79%;
  }
}
